import React, { useState } from 'react'
import { Card } from './Card'
import { Button } from './Button'
import { FiCopy, FiCheck, FiRefreshCw } from 'react-icons/fi'

interface PantunDisplayProps {
  pantun: string
  onGenerateNew: () => void
  isLoading?: boolean
}

export const PantunDisplay: React.FC<PantunDisplayProps> = ({
  pantun,
  onGenerateNew,
  isLoading = false
}) => {
  const [copied, setCopied] = useState(false)

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(pantun)
      setCopied(true)
      setTimeout(() => setCopied(false), 2000)
    } catch (err) {
      console.error('Failed to copy text: ', err)
    }
  }

  const formatPantun = (text: string) => {
    return text.split('\n').map((line, index) => (
      <div key={index} className="mb-2 last:mb-0">
        {line}
      </div>
    ))
  }

  return (
    <div className="modern-card p-10 text-center">
      <div className="mb-8">
        <h3 className="text-3xl font-bold text-orange-900 mb-6">
          Pantun Anda
        </h3>
        <div className="pantun-display bg-gradient-to-br from-orange-50 to-yellow-50 rounded-2xl p-8 shadow-inner border-2 border-orange-200">
          {formatPantun(pantun)}
        </div>
      </div>
      
      <div className="flex flex-col sm:flex-row gap-6 justify-center">
        <Button
          onClick={handleCopy}
          variant="secondary"
          className="flex items-center justify-center gap-3 min-w-[160px]"
        >
          {copied ? (
            <>
              <FiCheck className="w-5 h-5" />
              Tersalin!
            </>
          ) : (
            <>
              <FiCopy className="w-5 h-5" />
              Salin
            </>
          )}
        </Button>
        
        <Button
          onClick={onGenerateNew}
          variant="accent"
          disabled={isLoading}
          className="flex items-center justify-center gap-3 min-w-[160px]"
        >
          {isLoading ? (
            <>
              <FiRefreshCw className="w-5 h-5 animate-spin" />
              Membuat...
            </>
          ) : (
            <>
              <FiRefreshCw className="w-5 h-5" />
              Buat Lagi
            </>
          )}
        </Button>
      </div>
    </div>
  )
}
