'use client'

import React, { useState } from 'react'
import { ModeSelector } from './ModeSelector'
import { PantunDisplay } from './PantunDisplay'
import { TextArea } from './TextArea'
import { Button } from './Button'
import { Card } from './Card'

type Mode = 'random' | 'continue' | 'mood'

interface PantunGeneratorProps {}

export const PantunGenerator: React.FC<PantunGeneratorProps> = () => {
  const [selectedMode, setSelectedMode] = useState<Mode | null>(null)
  const [input, setInput] = useState('')
  const [mood, setMood] = useState('')
  const [generatedPantun, setGeneratedPantun] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')

  const handleModeSelect = (mode: string) => {
    setSelectedMode(mode as Mode)
    setInput('')
    setMood('')
    setGeneratedPantun('')
    setError('')
  }

  const handleGenerate = async () => {
    if (!selectedMode) return

    setIsLoading(true)
    setError('')

    try {
      const response = await fetch('/api/generate-pantun', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          mode: selectedMode,
          input: selectedMode === 'continue' ? input : undefined,
          mood: selectedMode === 'mood' ? mood : undefined,
        }),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Terjadi kesalahan')
      }

      setGeneratedPantun(data.pantun)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Terjadi kesalahan')
    } finally {
      setIsLoading(false)
    }
  }

  const handleGenerateNew = () => {
    setGeneratedPantun('')
    setError('')
  }

  const renderInputArea = () => {
    if (!selectedMode) return null

    switch (selectedMode) {
      case 'continue':
        return (
          <div className="modern-card p-8">
            <TextArea
              label="Masukkan baris pantun yang sudah ada (1-3 baris):"
              value={input}
              onChange={setInput}
              placeholder="Contoh:&#10;Ikan lele minum bir&#10;Ikan hiu makan tomat"
              rows={3}
            />
          </div>
        )
      
      case 'mood':
        return (
          <div className="modern-card p-8">
            <TextArea
              label="Ceritakan suasana hati Anda:"
              value={mood}
              onChange={setMood}
              placeholder="Contoh: Sedih karena kehilangan, bahagia karena berhasil, atau galau karena cinta..."
              rows={3}
            />
          </div>
        )
      
      case 'random':
        return (
          <div className="modern-card p-8 text-center">
            <p className="text-orange-700 text-lg font-medium">
              Klik tombol "Buat Pantun" untuk menghasilkan pantun acak
            </p>
          </div>
        )
      
      default:
        return null
    }
  }

  const canGenerate = () => {
    switch (selectedMode) {
      case 'continue':
        return input.trim().length > 0
      case 'mood':
        return mood.trim().length > 0
      case 'random':
        return true
      default:
        return false
    }
  }

  return (
    <div className="max-w-6xl mx-auto px-4 py-8">
      {/* Header */}
      <div className="text-center mb-12">
        <h1 className="text-5xl md:text-7xl font-bold gradient-text mb-6 text-shadow-orange">
          Pantun Generator
        </h1>
        <p className="text-xl md:text-2xl text-orange-800 font-semibold max-w-3xl mx-auto mb-8">
          Mau bikin pantun apa hari ini?
        </p>
        
        {/* Cute T-Rex Character */}
        <div className="trex-character">
          <svg viewBox="0 0 200 200" className="w-full h-full">
            {/* T-Rex Body */}
            <ellipse cx="100" cy="120" rx="45" ry="35" fill="#ff6b35" stroke="#e55a2b" strokeWidth="2"/>
            {/* T-Rex Head */}
            <ellipse cx="100" cy="80" rx="35" ry="30" fill="#ff6b35" stroke="#e55a2b" strokeWidth="2"/>
            {/* T-Rex Eyes */}
            <circle cx="90" cy="75" r="4" fill="#2c1810"/>
            <circle cx="110" cy="75" r="4" fill="#2c1810"/>
            <circle cx="92" cy="73" r="1.5" fill="white"/>
            <circle cx="112" cy="73" r="1.5" fill="white"/>
            {/* T-Rex Mouth */}
            <path d="M 85 90 Q 100 100 115 90" stroke="#2c1810" strokeWidth="2" fill="none"/>
            {/* T-Rex Teeth */}
            <path d="M 90 90 L 92 95 L 94 90" fill="#2c1810"/>
            <path d="M 110 90 L 112 95 L 114 90" fill="#2c1810"/>
            {/* T-Rex Arms */}
            <ellipse cx="70" cy="110" rx="8" ry="15" fill="#ff6b35" stroke="#e55a2b" strokeWidth="2"/>
            <ellipse cx="130" cy="110" rx="8" ry="15" fill="#ff6b35" stroke="#e55a2b" strokeWidth="2"/>
            {/* T-Rex Legs */}
            <ellipse cx="85" cy="150" rx="12" ry="20" fill="#ff6b35" stroke="#e55a2b" strokeWidth="2"/>
            <ellipse cx="115" cy="150" rx="12" ry="20" fill="#ff6b35" stroke="#e55a2b" strokeWidth="2"/>
            {/* T-Rex Tail */}
            <ellipse cx="145" cy="130" rx="25" ry="15" fill="#ff6b35" stroke="#e55a2b" strokeWidth="2" transform="rotate(15 145 130)"/>
            {/* Cute blush */}
            <circle cx="80" cy="85" r="3" fill="#ff8a80" opacity="0.7"/>
            <circle cx="120" cy="85" r="3" fill="#ff8a80" opacity="0.7"/>
          </svg>
        </div>
      </div>

      {/* Mode Selection */}
      <ModeSelector
        selectedMode={selectedMode}
        onModeSelect={handleModeSelect}
      />

      {/* Input Area */}
      {selectedMode && (
        <div className="mb-8">
          {renderInputArea()}
          
          <div className="flex justify-center mt-6">
            <Button
              onClick={handleGenerate}
              disabled={!canGenerate() || isLoading}
              size="lg"
              className="min-w-[200px]"
            >
              {isLoading ? 'Membuat Pantun...' : 'Buat Pantun'}
            </Button>
          </div>
        </div>
      )}

      {/* Error Display */}
      {error && (
        <Card className="p-4 mb-6 bg-red-50 border-red-200">
          <p className="text-red-600 text-center">{error}</p>
        </Card>
      )}

      {/* Result Display */}
      {generatedPantun && (
        <PantunDisplay
          pantun={generatedPantun}
          onGenerateNew={handleGenerateNew}
          isLoading={isLoading}
        />
      )}
    </div>
  )
}
